// 获取文章列表
Mock.mock('/my/article/list', 'get', (options) => {
  const body = JSON.parse(options.body)
  const cateid = body.cate_id
  if (!cateid) {
    return {
      code: 0,
      message: '获取文章列表成功！',
      total: articlelist.length,
      data: articlelist
    }
  }

  const checkUser = articlelist.filter(p => {
    return String(p.cateid).indexOf(String(cateid)) !== -1
  })
  // 没有查询到返回失败信息
  if (!checkUser) {
    return { code: 201, data: { message: '未分类' } }
  }
  // 如果有返回成功信息
  console.log(checkUser)
  return {
    code: 0,
    message: '获取文章列表成功！',
    total: checkUser.length,
    data: checkUser
  }
})
