// 发表文章
Mock.mock('/my/article/add', 'post', (options) => {
  const body = options.body
  const title = body.get('title')
  const content = body.get('content')
  const coverimg = body.get('cover_img')
  const state = body.get('state')
  const cateid = body.get('cate_id')

  const res = JSON.parse(localStorage.getItem('res'))

  const length = articlelist.length
  const id = articlelist[length - 1].id + 1
  const result = {
    id: id,
    title: title,
    content: content,
    cover_img: coverimg,
    state: state,
    cate_id: cateid
  }
  res.push(result)
  localStorage.setItem('res', JSON.stringify(res))
  const myDate = new Date()
  let index1 = 0
  articlelist.forEach((item, index, array) => {
    if (item.id === cateid) {
      index1 = index
    }
  })
  const name1 = articlelist[index1].cate_name

  const result1 = {
    id: id,
    title: title,
    pub_date: myDate,
    state: state,
    cateid: cateid,
    cate_name: name1
  }
  articlelist.push(result1)
  console.log('articlelist', articlelist)
  return {
    code: 0,
    message: '请求文章详情成功！'
  }
})
// 删除文章分类
Mock.mock('/my/article/info', 'delete', (options) => {
  const body = JSON.parse(options.body)
  let index1 = 0
  let index2 = 0
  articlelist.forEach((item, index, array) => {
    if (item.id === body.id) {
      index1 = index
    }
  })
  articlelist.splice(index1, 1)
  const res = JSON.parse(localStorage.getItem('res'))
  res.forEach((item, index, array) => {
    if (item.id === body.id) {
      index2 = index
    }
  })
  res.splice(index2, 1)
  localStorage.setItem('res', JSON.stringify(res))
  return {
    code: 0,
    message: '删除文章分类成功！'
  }
})

