// 增加数据接口：
Mock.mock('/api/addInfo', 'post', options => {
  const body = JSON.parse(options.body) // 获取请求参数
  const id = parseInt(body.id)
  let flag = true

  for (const item of user) {
    if (item.id === id) flag = false // 判断id是否已经存在
  }

  // 如果id不存在
  if (flag) {
    user.push(
      {
        product: body.product,
        id
      }
    )
    return {
      user,
      status: 200,
      msg: '添加成功'
    }
  }
  // 如果id已存在
  return {
    status: 400,
    msg: '添加失败，id已存在'
  }
})

// 删除数据接口
Mock.mock('/api/remove', 'delete', options => {
  const id = parseInt(JSON.parse(options.body).id) // 获取请求id
  let index = null

  for (const i in user) {
    if (user[i].id === id) index = i // 获取id对应的index
  }

  if (index !== null) {
    console.log(`删除的id为：${id}，对应的index为：${index}`)
    user.splice(index, 1) // 从index开始删除一个（本身）
    return {
      user,
      status: 200,
      msg: '删除成功'
    }
  }
  return {
    status: 400,
    msg: '删除失败，id不存在'
  }
})

// 修改数据接口
Mock.mock('/api/edit', 'put', options => {
  const body = JSON.parse(options.body) // 解析请求参数（只能解析到这）
  const id = parseInt(body.id) // 获取请求id
  let index = null

  for (const i in user) {
    if (user[i].id === id) { // 判断id是否已经存在
      index = i // 拿到id对应的index
    }
  }

  // 如果id存在
  if (index != null) {
    user[index].product = body.product
    return {
      user,
      status: 200,
      msg: '修改成功'
    }
  }
  return {
    status: 400,
    msg: '修改失败'
  }
})
